// 편집기(에디터 스타일)

@import 'editor/editor-common'; // 편집기 기본 스타일
@import 'editor/editor-sidebar'; // 편집기 우측 사이드 메뉴 스타일

.block-editor-block-list__layout {
  .block-editor-block-list__block {
    .block-editor-block-drop-zone {
      bottom: -3px;
      margin: 0 14px;
      top: -4px;
    }
  }
}

/* Width of the editor column */
.xe-blockeditor__editor {
  // .edit-post-visual-editor,
  .editor-post-title__block,
  .block-editor-default-block-appender,
  .block-editor-block-list__block {
    margin-left: auto;
    margin-right: auto;
    max-width: 1056px !important; /* width of page (1034px) plus 2 x 6px padding */
  }

  .edit-post-text-editor {
    margin-left: 20px;
    margin-right: 0;
  }
}


